import React from "react";
import styled from "./Footer.module.scss";
import classNames from "classnames/bind";
import CheckIcon from '@mui/icons-material/Check';
function Footer() {
  const cx = classNames.bind(styled);
  return (
    <div>
      <div className={cx("Footer")}>
        <div className="container">
            <div className={cx("flex")}>
              <div className={cx("Footer-content-flex")}>
                <div className={cx("Footer-content")}>
                  <div className={cx("Footer-content-title")}>
                    <h3>H TR DCH V</h3>
                  </div>
                  <div className={cx("Footer-content-help")}>
                    <ul>
                        <li>Tng i CSKH, Bo Hng Wifi: <strong>1800.8119</strong></li>
                        <li>Tng i CSKH, Dch V Di ng: <strong>1800.8098</strong></li>
                        <li>Lp t Nhanh Trong Vng 24h-48h</li>
                    </ul>
                  </div>
                </div>
                <div className={cx("Footer-service")}>
                  <div className={cx("Footer-service-title")}>
                    <h3>DCH V LIN KT KHC</h3>
                  </div>
                  <div className={cx("Footer-service-other")}>
                    <ul>
                        <li>
                            <CheckIcon/>
                            <p>Lp mng Viettel 63 tnh</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>Ch K S ViettelCA</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>Di ng Tr Sau</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>ng K 4G Viettel</p>
                            <div>New</div>
                        </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      <div className={cx("Footer-title")}>Copyright 2023 Vietteltelecom</div>
    </div>
  );
}

export default Footer;
